body {
    font-size: 10pt;    
}

.folders-section {
    background-color: #B4B4B4;
    border-radius: 10px;
}

.folderBtn {
    background-color: white;
    color: #333;
    font-size: 10pt;
    letter-spacing: 1px; 
    border-radius: 6px; 
    border: none;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.folderBtn:hover {
    -webkit-box-shadow: rgba(180, 135, 135, 0.2) 0px 0px 15px;
            box-shadow: rgba(180, 135, 135, 0.2) 0px 0px 15px;  
    background-color: white;
    color: #333;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.topRow {
    background-color: #333;
    border-radius: 10px;    
    color: white;
    letter-spacing: 2px;
    font-weight: 0;
}

.myTable {
    background-color: white;
    border-radius: 10px;
    -webkit-box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 10px;
            box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 10px;
}

.fileBtns {
    float: right;
}

.fileBtns .btn {
    -webkit-box-shadow: rgba(180, 135, 135, 0.2) 0px 0px 10px;
            box-shadow: rgba(180, 135, 135, 0.2) 0px 0px 10px;          
}

.search {
    float: left;
}

.add-file-btn, .add-file-btn:hover, .add-file-btn:visited, .add-file-btn:active {
    color: white;
    text-decoration: none;
}


